.container{
    padding-top: 80px;
    padding-bottom: 80px;
  }
  
  * {
    margin: 0;
    padding: 0;
  }
  
  *, *:before, *:after {
    box-sizing: border-box;
  }
  
  body {
    font: 1em/1 'Open Sans', sans-serif;
    color: #555;
  }
  
  
  
  ul {
    width: 800px;
    margin: 40px auto;
    list-style: none;
  }
  
  li {
    display: -webkit-flex;
    display: flex;
  }
  
  li:last-child {
    border: 0;
  }
  
  li p {
    display: block;
    padding: 20px;
    padding-left: 50px;
    line-height: 1.4;
  }
  
  li p:nth-child(1) {
    position: relative;
    padding-right: 25px;
    font-size: 16px;
    line-height: 2.5;
    color: #565656;
  }
  
  li p:nth-child(1):after {
    content: '';
    position: absolute;
    top: 28px;
    right: -12px;
    display: block;
    width: 20px;
    height: 20px;
    border: 5px solid #daad2c9e;
    border-radius: 50%;
    background: #fff;
  }
  
  
  li p:nth-child(2) {
    -webkit-flex: 1 1;
            flex: 1 1;
    font-size: 20px;
    border-left: 5px solid #daad2c9e;
  }
  
  li p span {
    display: block;
    margin: 5px 0 0;
    font-size: 12px;
    /* font-style: italic; */
    
  }
  
  
